@import url(https://fonts.googleapis.com/css?family=Quicksand|Ubuntu);

*{
    margin: 0;
    padding: 0;
    font-family: ubuntu;
    box-sizing: border-box;
    --button-color: #14AECF;
}
h1, h5{
    margin: 2em;
    text-align: center;
}
.App{
    max-width: 100vw;
    font-size: 15px;
    position: relative;
    .social{
        padding: 0 .5em;
        font-size: .6em;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        -webkit-align-items: center;
                align-items: center;
        .left{
            display: -webkit-flex;
            display: flex;
            -webkit-align-items: center;
                    align-items: center;
            .icon{
                width: 1.9vh;
                height: 1.9vh;
                &.facebook{
                    width: 1.7vh;
                    height: 1.7vh;
                }
                &.twitter{
                    height: 1.6vh;
                    width: 2.8vh;
                }
                &:hover{
                    cursor: pointer;
                }
            }
            *{
                margin: .4em;
            }
        }
    }
    button{
        display: inline;
        padding: 1em;
        background-color: var(--button-color);
        color: white;
        border: 0;
        &:hover{
            background-color: aquamarine;
            color: black;
            cursor: pointer;
        }
    }
    .clickable{
        cursor: pointer;
        margin: .3em;
    }
    background-color: rgb(58, 58, 58);
    .header{
        z-index: 2;
        width: 100%;
        position: fixed;
        .social{
            color: white;
            background-color: rgb(0, 0, 0);
            height: 3vh;
        }
        .nav{
            background-color: rgb(255, 255, 255);
            height: 7vh;
        }
    }
    .mainImg{
        height: 80vh;
        position: relative;
        top: 10vh;
        #mainImgContainer{
            position: relative;
            height: 100%;
            width: 100%;
            background: rgb(104, 104, 104) url(/annpage/static/media/annette_dark.92a0280e.jpg) no-repeat;
            background-blend-mode: multiply;
            background-position: 65% 50%;
            background-size: cover;
            background-attachment: top left;
        }
        .text{
            display: grid;
            justify-items: center;
            color: white;
            position: absolute;
            top: 8%;
        }
    }
    .callMe{
        position: relative;
        padding: 1em 2em;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        -webkit-align-items: center;
                align-items: center;
        background-color: white;
    }
    .direction{
        color: white;
        position: relative;
        display: grid;
        grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
        .cardContainer{
            position: relative;
            display: -webkit-flex;
            display: flex;
            -webkit-flex-direction: column;
                    flex-direction: column;
            -webkit-align-items: center;
                    align-items: center;
            padding: 3em;
            .cardImage{
                position: absolute;
                margin: 0;
                top: 0;
                left: 0;
                width: 100%;
                height: 100%;
                background: url(/annpage/static/media/scotland.4c995dc2.jpg) no-repeat;
                background-blend-mode: multiply;
                background-position: 15% 50%;
                background-size: cover;
                background-attachment: top left;
                z-index: 0;
                -webkit-animation: darken 10s forwards;
                        animation: darken 10s forwards;
                &:hover{
                    background-color: yellow;
                    cursor: pointer;
                }
            }
            &.one .cardImage{
                background: url(/annpage/static/media/ptsdwords.0b840774.jpg) no-repeat;
                -webkit-animation: darken 4s forwards;
                        animation: darken 4s forwards;
                background-blend-mode: multiply;
                background-position: 50% 30%;
                background-size: cover;
            }
            *{
                position: relative;
                left: 150vw;
                z-index: 1;
                margin: .7em 1em;
                -webkit-animation: slideLeft 2s 4s forwards;
                        animation: slideLeft 2s 4s forwards;
            }
            button{
                width: -webkit-fit-content;
                width: -moz-fit-content;
                width: fit-content;
                background-color: transparent;
                border: 2px solid white;
                &:hover{
                    background-color: white;
                    color: black;
                    cursor: pointer;
                }
            }
        }
    }
    .aboutMe{
        position: relative;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
        background-color: white;
        padding: 1em;
        img{
            height: 30vh;
            width: 30vh;
            border-radius: 50%;
            margin: 2em;
        }
        div{
            h4{
                text-align: center;
            }
            h5{
                text-align: center;
                color: gray;
                margin-bottom: 1em;
            }
            & p:nth-child(2){
                color: blue;
            }
        }

    }
    .subscribe{
        padding: 3em 0;
        font-size: .8em;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
        background-color: #14aecf;
        color: white;
        button{
            color: #14aecf;
            background-color: white;
            border: 2px solid white;
        }
        *{
            margin: 1em;
        }
    }
    footer{
        font-size: .5em;
    }
}
@media (min-width: 750px){
    .App{
        font-size: 20px;
        .mainImg{
            .text{
                top: 15vh;
            }
        }
        .aboutMe{
            display: grid;
            grid-template-columns: 30vw 1fr;
            grid-gap: 1em;
            padding: 2em;
            background-color: white;
            padding: 1em;
            img{
                margin: 0;
                height: auto;
                width: 100%;
                border-radius: 50%;
            }
            div{
                h5{
                    text-align: center;
                    color: gray;
                    margin-bottom: 1em;
                }
            }
    
        }
    }
}
@media (min-width: 1500px){
    .App{
        font-size: 28px;

    }
}
@-webkit-keyframes darken {
  from{background-color: white;}
  to{background-color: rgb(102, 102, 102);}
    
}

@keyframes darken {
  from{background-color: white;}
  to{background-color: rgb(102, 102, 102);}
    
}
@-webkit-keyframes slideLeft {
  from{left: 100vw; opacity: 0}
  to{left: 0px; opacity: 1}
    
}
@keyframes slideLeft {
  from{left: 100vw; opacity: 0}
  to{left: 0px; opacity: 1}
    
}
